:root {
  --bs-heading-color: #424242 !important;
  --bs-link-color-rgb: #424242 !important;
}

body[data-bs-theme=light] main a {
  color: #424242;
}
body[data-bs-theme=light] main a:hover {
  color: black;
}
body[data-bs-theme=dark] main a {
  color: rgba(173, 181, 189, 0.7490196078);
}
body[data-bs-theme=dark] main a:hover {
  color: white;
}

@font-face {
  font-family: "Outfit";
  src: url("../fonts/outfit/Outfit-VariableFont_wght.ttf");
}
body {
  font-family: "Outfit", sans-serif !important;
  font-optical-sizing: auto;
  font-style: normal;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 700;
}

#back-to-top {
  width: 10rem;
  right: 0;
  left: auto;
  padding-left: 0 !important;
}

.footer-logos {
  max-width: 400px;
}

.container {
  max-width: 860px;
}

audio {
  max-height: 100%;
  max-width: 100%;
  margin: auto;
  object-fit: contain;
}

@media (min-width: 768px) {
  ul.column-list {
    column-count: 2;
    column-gap: 2rem;
  }
}
body {
  font-size: 1.2rem;
}